@import url("https://fonts.googleapis.com/css2?family=Source+Code+Pro&display=swap");

img {
  max-height: 560px;
}

.meme {
  max-width: 400px;
  margin: 0 auto;
}

.signature {
  text-align: right;
  font-family: monospace;
}

.bmc-block {
  padding: 6px 0;

  .bmc-greeting {
    font-size: 14px;
    line-height: 37px;
  }
}

.post h2 {
  border-left: solid #2980b9 6px;
  padding: 8px 16px;
}

.ref {
  font-size: 0.8em;
  color: rgba($color: #000000, $alpha: 0.3);
}

.likecoin-button {
  margin: 0 !important;
}

iframe {
  max-width: 100%;
}

/** side-navi */
$side-navi-color: rgba(0, 0, 0, 0.6);
@media only screen and (max-width: 90rem) {
  #side-nav {
    display: none;
  }
}
#side-nav {
  min-width: 8rem;
  max-width: 10rem;
  position: fixed;
  top: 0;
  margin-left: 44rem;
  padding-top: 6rem;

  .head {
    color: $side-navi-color;
    border-bottom: solid 1px $side-navi-color;
    margin-bottom: 8px;
  }

  ul {
    padding-left: 0;
  }

  a {
    color: $side-navi-color;
  }
  .nav2 {
    list-style: none;
    font-size: 80%;
  }
  .nav3 {
    list-style: none;
    font-size: 70%;
    padding-left: 8px;
  }
}
